const CreateTree = value =>{
    return {
        date:value,
        children:null,
        parent:null
    }
}
//åˆ›å»ºæ ‘å°±æ˜¯åˆ›å»ºä¸€ä¸ªèŠ‚ç‚¹ï¼Œç»™å‡ºå€¼å°±è¡Œ


const addTree = (node,value) =>{
    const newNode ={
        date:value,
        children:null,
        parent:node
    }
    node.children = node.children || []  //åšä¸ªä¿åº•ï¼ŒèŠ‚ç‚¹å¯èƒ½ä¸ºç©ºä¹Ÿä¸èƒ½ä¸ºè‡ªå·±ï¼Œå¦‚æœä¸æ˜¯ç©ºï¼Œå°±è®©å®ƒç­‰äºè‡ªå·±
    node.children.push(newNode)
    return newNode   //è¿”å›èŠ‚ç‚¹,ä¸ºå­æ ‘åœ¨åŠ æ–°çš„èŠ‚ç‚¹
}
//å¢åŠ èŠ‚ç‚¹ï¼Œåˆ›å»ºä¸€ä¸ªæ–°çš„èŠ‚ç‚¹ï¼ˆchildrenï¼‰ï¼Œæ·»åŠ åˆ°å·²çŸ¥çš„èŠ‚ç‚¹ä¸Šï¼ˆparentï¼‰


const removeNode=(tree,node) =>{
    const siblings = node.parent.children   //è¿™ä¸ªèŠ‚ç‚¹çš„çˆ¶äº²çš„æ‰€æœ‰å„¿å­ï¼Œä¹‹å‰å†™æˆtree.node.childrenæŠ¥é”™ï¼Œå› ä¸ºè¿™æ˜¯æ‰¾å®ƒçš„å„¿å­äº†   
    let index = 0
    for(let i = 0;i < siblings.length;i++){
        if(siblings[i]===node){
            index = i
        }
    }
    siblings.splice(index,1)
}
//åˆ é™¤çš„èŠ‚ç‚¹é‡ç‚¹æ˜¯ï¼Œæ‰¾åˆ°èŠ‚ç‚¹çš„parentï¼Œéå†é‡Œé¢çš„child,ç„¶ååˆ é™¤è¿™ä¸ªchildçš„å†…å­˜åœ°å€ï¼Œä¸èƒ½å°†å…¶åæˆnull

const travel = (tree,fn) =>{
    fn(tree)
    if(!tree.children){
        return
    }
    //å¦‚æœchildrenä¸ºnullï¼Œä»€ä¹ˆä¹Ÿä¸åš
    for(let i=0;i<tree.children.length;i++){
        travel(tree.children[i],fn)
    }
}
//å…ˆæŠŠæ ¹èŠ‚ç‚¹éå†ä¸€éï¼Œå†æŠŠå­èŠ‚ç‚¹ä½œä¸ºä¸€é¢—æ ‘éå†ä¸€é


